.main-screen {
  width: 100%;
  height: 100vh;
  background-image: url(../images/main-screen.png);
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 480px) {
    background-image: url(../images/main-screen_mobile.png);
  }
  &_text {
    color: #fff;
  }
  &_subtitle {
    font-size: 2.5rem;
    @media (max-width: 480px) {
      font-size: 1.875rem;
    }
  }
  &_title {
    font-size: 2.5rem;
    font-weight: 400;
    @media (max-width: 480px) {
      font-size: 1.875rem;
      margin-bottom: 50px;
    }
    span {
      font-size: 100px;
      @media (max-width: 1024px) {
        font-size: 88px;
      }
      @media (max-width: 480px) {
        font-size: 60px;
      }
    }
  }
}

.btn-white-border {
  font-size: 1.125rem;
  font-weight: 300;
  color: #fff;
  background: none;
  border: 1px solid $border;
  padding: 8px 22px;
  cursor: pointer;
  margin: 0 auto;
  display: block;
  transition: all 0.2s ease;
  text-decoration: none;
  width: 170px;
  text-align: center;
  &:hover {
    background: $accent;
    border-color: $accent;
  }
}

.coast {
  display: flex;
  width: 100%;
  height: 100vh;
  max-height: 1024px;
  overflow: hidden;
  // background-color: $fonts;
  background-image: url(../images/map4.png);
  background-size: cover;
  @media (max-width: 480px) {
    background-position: center;
  }
  &_item {
    width: 50%;
    padding: 140px 100px;
    transition: all 0.4s ease;
    background-color: rgba(0,0,0,0.1);
    position: relative;
    @media (max-width: 768px) {
      padding: 80px 30px 80px 80px;
    }
    @media (max-width: 480px) {
      padding: 60px 10px;
    }
    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      background: $fonts;
      z-index: 1;
      opacity: 0.6;
      transition: all 0.4s ease;
      opacity: 0;
    }
    &:after {
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      background-color: $blue;
      opacity: 0;
      z-index: 1;
      transition: all 0.4s ease;
    }
  }
  &_title {
    font-size: 1.875rem;
    text-transform: uppercase;
    color: #fff;
    font-weight: 400;
    transition: all 0.4s ease;
    text-align: left;
    position: relative;
    z-index: 10;
    margin-bottom: 40px;
  }
  &_text {
    color: #fff;
    width: 80%;
    max-width: 860px;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    transition: all 0.1s ease;
    transform: translateY(30px);
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  &_subtitle {
    font-size: 3.125rem;
    font-weight: 300;
    @media (max-width: 480px) {
      font-size: 22px;
    }
  }
  &_description {
    font-size: 1.125rem;
    line-height: 1.875rem;
    margin-bottom: 40px;
    font-weight: 300;
    @media (max-width: 480px) {
      font-size: 1rem;
      line-height: 140%;
    }
  }
  &_city {
    font-weight: 300;
    span {
      color: #D2D4DA;
      text-transform: uppercase;
      display: inline-block;
      margin-right: 10px;
      font-weight: 500;
    }
  }
}

.west-coast {
  // background-image: url(../images/west-coast.jpg);
  // background-position: left top;
  border-right: 1px solid #fff;
}
.east-coast {
  // background-image: url(../images/east-coast.jpg);
  // background-position: right top;
}

.coast_item {
  &.show {
    width: 90%;
    display: block;
    padding: 140px 100px;
    @media (max-width: 1024px) {
      padding: 80px 80px;
    }
    @media (max-width: 768px) {
      padding: 30px;
    }
    .coast_title {
      font-size: 1.25rem;
      color: #D2D4DA;
      @media (max-width: 768px) {
        font-size: 12px;
      }
    }
    .coast_text {
      visibility: visible;
      opacity: 1;
      transition: all 0.4s ease 0.3s;
      position: relative;
      z-index: 10;
      transform: translateY(0px);
    }
    &:before {
      opacity: 0.5;
    }
  }
  &.hide {
    display: block;
    padding: 140px 30px;
    width: 10%;
    text-align: center;
    @media (max-width: 1024px) {
      padding: 80px 10px;
    }
    @media (max-width: 768px) {
      padding: 30px 10px;
      min-width: 98px;
    }
    .coast_title {
      font-size: 1.25rem;
      @media (max-width: 768px) {
        font-size: 12px;
      }
    }
    &:after {
      opacity: 0.9;
    }
  }
}

.blocks {
  padding-top: 100px;
  padding-bottom: 80px;
  @media (max-width: 480px) {
    padding-bottom: 30px;
  }
  &_title {
    text-align: center;
    font-size: 100px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 20px;
    @media (max-width: 1024px) {
      font-size: 80px;
    }
    @media (max-width: 768px) {
      font-size: 70px;
      margin-bottom: 15px;
    }
    @media (max-width: 480px) {
      font-size: 50px;
    }
  }
  &_subtitle {
    color: $fonts-light;
    font-size: 1.25rem;
    display: block;
    margin-bottom: 65px;
    text-align: center;
  }
}

.block {
  display: flex;
  margin-bottom: 80px;
  @media (max-width: 768px) {
    flex-direction: column;
  }
  &:nth-child(even) {
    .block_text {
      padding: 10px 136px 10px 10px;
      @media (max-width: 1300px) {
        padding: 10px 66px 10px 10px;
      }
      @media (max-width: 1024px) {
        padding: 0px 40px 0px 0px;
      }
      @media (max-width: 768px) {
        padding: 30px 0 0 0;
      }
      @media (max-width: 480px) {
        padding: 20px 0 0 0;
      }
    }
  }
  &_img {
    width: 500px;
    min-width: 500px;
    height: 500px;
    @media (max-width: 1024px) {
      width: 300px;
      min-width: 300px;
      height: 300px;
    }
    @media (max-width: 768px) {
      order: -1;
    }
    @media (max-width: 480px) {
      width: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &_text {
    padding: 10px 10px 10px 136px;
    @media (max-width: 1300px) {
      padding: 10px 10px 10px 66px;
    }
    @media (max-width: 1024px) {
      padding: 0px 0px 0px 40px;
    }
    @media (max-width: 768px) {
      padding: 30px 0 0 0;
    }
    @media (max-width: 480px) {
      padding: 20px 0 0 0;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &_title {
    font-size: 2.875rem;
    font-weight: 400;
    margin-bottom: 20px;
    @media (max-width: 1024px) {
      font-size: 2.5rem;
      margin-bottom: 15px;
    }
    @media (max-width: 480px) {
      font-size: 2rem;
      margin-bottom: 10px;
    }
  }
  &_subtitle {
    color: $fonts-light;
    font-size: 1.125rem;
    padding-left: 50px;
    display: block;
    margin-bottom: 30px;
    @media (max-width: 1024px) {
      font-size: 1rem;
      margin-bottom: 15px;
    }
    @media (max-width: 480px) {
      padding-left: 30px;
    }
  }
  &_description {
    line-height: 1.875rem;
    font-size: 1.125rem;
    @media (max-width: 1024px) {
      font-size: 1rem;
      line-height: 150%;
    }
  }
}

.form-block {
  background-image: url(../images/form-block_bg.png);
  background-attachment: fixed;
  padding-top: 130px;
  padding-bottom: 160px;
  @media (max-width: 480px) {
    padding-top: 100px;
    padding-bottom: 120px;
  }
  &_title {
    font-weight: 400;
    font-size: 3.75rem;
    text-align: center;
    margin-bottom: 20px;
    text-transform: uppercase;
    @media (max-width: 480px) {
      font-size: 2.5rem;
      margin-bottom: 10px;
    }
  }
  &_subtitle {
    font-size: 1.25rem;
    text-align: center;
    display: block;
    margin-bottom: 65px;
    @media (max-width: 480px) {
      font-size: 1rem;
      margin-bottom: 40px;
    }
  }
}

.form {
  display: flex;
  justify-content: center;
  input {
    background: rgba(38, 46, 46, 0.36);
    border: 1px solid #3F81A3;
    width: 475px;
    height: 62px;
    padding: 19px 50px;
    color: #fff;
    font-size: 1.25rem;
    margin-right: 13px;
    font-weight: 300;
    @media (max-width: 480px) {
      font-size: 1rem;
      height: 40px;
      padding: 5px 20px;
      margin-right: 5px;
    }
    &::placeholder {
      color: #fff;
    }
  }
  button {
    color: #E5E5E5;
    font-size: 1.25rem;
    font-weight: bold;
    background: #3F81A3;
    width: 170px;
    height: 62px;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease;
    @media (max-width: 480px) {
      font-size: 1rem;
      height: 40px;
      padding: 5px 20px;
      width: 120px;
    }
    &:hover {
      background-color: $accent;
    }
  }
}

.about {
  padding-top: 170px;
  padding-bottom: 140px;
  @media (max-width: 480px) {
    padding-top: 100px;
    padding-bottom: 80px;
  }
}
.title {
  font-weight: 400;
  text-transform: uppercase;
  font-size: 3.75rem;
  margin-bottom: 45px;
  @media (max-width: 480px) {
    font-size: 2.5rem;
  }
}
.description {
  font-size: 1.125rem;
  line-height: 1.75rem;
  max-width: 680px;
}

.team {
  padding-top: 80px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  &_item {
    width: 25%;
    padding: 10px 50px;
    text-align: center;
    @media (max-width: 1200px) {
      width: 50%;
      margin-bottom: 30px;
    }
    @media (max-width: 768px) {
      padding: 10px 20px;
    }
    @media (max-width: 480px) {
      padding: 4px 20px;
    }
    &:hover {
      .team_img img {
        opacity: 0.9;
      }
      .team_links {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &_img {
    width: 220px;
    height: 220px;
    border-radius: 50%;
    margin: 0 auto 42px auto;
    background-color: $fonts;
    @media (max-width: 768px) {
      width: 150px;
      height: 150px;
      margin-bottom: 20px;
    }
    @media (max-width: 480px) {
      width: 100px;
      height: 100px;
    }
    img {
      transition: all 0.2s ease;
    }
  }
  &_title {
    font-size: 1.25rem;
    font-weight: 400;
    margin-bottom: 25px;
    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
    @media (max-width: 480px) {
      margin-bottom: 5px;
    }
  }
  &_subtitle {
    font-size: 1rem;
    color: $fonts-light;
  }
  &_links {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s ease;
  }
  &_link {
    display: block;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @media (max-width: 480px) {
      width: 15px;
      height: 15px;
      margin-right: 7px;
    }
    &.fb {
      background-image: url(../images/sprite.svg#fb);
    }
    &.inst {
      background-image: url(../images/sprite.svg#inst);
    }
    &.vk {
      background-image: url(../images/sprite.svg#vk);
    }
    &:last-child {
      margin-right: 0;
    }
    &:hover {
      opacity: 0.9;
    }
  }
}

.gallery {
  &_list {
    display: flex;
    flex-wrap: wrap;
  }
  &_item {
    width: 25%;
    height: 528px;
    background-color: $fonts;
    position: relative;
    @media (max-width: 480px) {
      width: 50%;
      height: 260px;
    }
    &:before {
      content: '';
      width: 30px;
      height: 30px;
      background-image: url(../images/sprite.svg#inst_white);
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -15px;
      margin-top: -15px;
      opacity: 0;
      z-index: 10;
    }
    img {
      transition: all 0.2s ease;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:hover {
      &:before {
        opacity: 1;
      }
      img {
        opacity: 0.8;
      }
    }
  }
}

.contact {
  padding-top: 180px;
  padding-bottom: 180px;
  background-image: url(../images/contact_bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  @media (max-width: 480px) {
    padding-top: 120px;
    padding-bottom: 120px;
  }
  .description {
    margin-bottom: 40px;
    max-width: 400px;
  }
  &_wrap {
    display: flex;
    @media (max-width: 768px) {
      display: block;
    }
  }
  &_column {
    width: 50%;
    @media (max-width: 768px) {
      width: 100%;
    }
  }
}

.contacts {
  @media (max-width: 768px) {
    margin-bottom: 40px;
  }
  &_item {
    display: flex;
    font-size: 1.125rem;
    line-height: 1.75rem;
    margin-bottom: 20px;
  }
  &_title {
    text-transform: uppercase;
    color: $blue;
    font-weight: 500;
    width: 90px;
  }
  &_description {
    a {
      display: block;
      text-decoration: none;
      color: $fonts;
      transition: all 0.2s ease;
      &:hover{
        color: $accent;
      }
    }
  }
}

.callback {
  text-align: center;
  max-width: 400px;
  @media (max-width: 1024px) {
    max-width: 300px;
  }
  input {
    background: rgba(38, 46, 46, 0.06);
    border: 1px solid #3F81A3;
    width: 400px;
    height: 60px;
    padding: 19px 50px;
    text-align: center;
    color: $blue;
    font-size: 1rem;
    font-weight: 300;
    display: block;
    margin-bottom: 30px;
    @media (max-width: 1024px) {
      width: 300px;
      height: 50px;
      margin-bottom: 20px;
    }
    @media (max-width: 480px) {
      width: 100%;
      max-width: 400px;
    }
    &::placeholder {
      color: $blue;
    }
  }
  button {
    color: #E5E5E5;
    font-size: 1.25rem;
    font-weight: 400;
    background: $blue;
    width: 240px;
    height: 60px;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease;
    @media (max-width: 1024px) {
      height: 50px;
    }
    &:hover {
      background-color: $accent;
    }
  }
}
