.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 50px;
  @media (max-width: 1600px) {
    padding-top: 25px;
  }
  &_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.h-logo {
  color: #fff;
  font-size: 1.25rem;
  text-decoration: none;
}

.social-icon {
  display: flex;
  &_item {
    text-decoration: none;
    display: block;
    margin-right: 13px;
    @media (max-width: 480px) {
      margin-right: 8px;
    }
    &:last-child {
      margin-right: 0;
    }
    svg {
      transition: all 0.2s ease;
      fill: #fff;
      @media (max-width: 480px) {
        width: 20px;
        height: 20px;
      }
    }
    &:hover {
      svg {
        fill: $accent;
      }
    }
  }
}
